<template>
  <section id="rolls">
    <div class="section-info">
      <h1>Роллы</h1>
      <p>Только свежие продукты и оригинальные рецепты от японских поваров</p>
    </div>
    <List @cartCount="cartCount" :items="rolls"></List>
  </section>
</template>

<script setup>
import { ref } from "vue";
import rollItems from "../data/rolls.json";
import List from "@/components/List.vue";

const rolls = ref(rollItems);

const emit= defineEmits(["cartCount"]);

const cartCount = (count) => emit('cartCount', count);

</script>
